.form {
  position: absolute;
  padding: 43px 106px 0 74px;
  top: -186px;
  left: 50%;
  transform: translateX(-50%);
  width: 580px;
  min-height: 580px;

  background-color: #FFFFFF;
}

.form__caption {
  margin: 0;
  margin-left: 75px;

  font-family: "Raleway", "Arial", monospace;
  font-weight: 900;
  font-size: 25px;
  line-height: 2;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #000000;
}

.form__row {
  margin-top: 44px;
  margin-bottom: 42px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form__column {
  width: 177px;
}

.form__label {
  display: block;
  margin-bottom: 4px;

  font-family: "Roboto", "Arial", monospace;
  font-weight: 400;
  font-size: 20px;
  line-height: 1;
  color: #000000;
}

.form__input {
  display: block;

  width: 100%;

  font-family: "Roboto", "Arial", monospace;
  font-weight: 400;
  font-size: 20px;
  line-height: 1;
  color: #000000;

  border: none;
  border-bottom: 1px solid #cccccc;

  &--mail {
    margin-bottom: 50px;
  }
}

.form__text {
  margin-top: 26px;
  margin-left: 70px;

  font-family: "Roboto", "Arial", monospace;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;

  color: #a3a3a3;
}

.form__link {
  display: block;
  margin-top: 3px;
  margin-left: 52px;
  width: 195px;

  text-decoration: none;
  color: #000000;
  opacity: 0.6;

  border-bottom: 1px dotted #000000;
  cursor: pointer;

  &:hover {
    opacity: 1;
  }
}

.form__submit {
  margin-left: 101px;
  margin-top: 34px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 230px;
  height: 60px;

  font-family: "Raleway", "Arial", monospace;
  font-weight: 900;
  font-size: 25px;
  line-height: 2;
  color: #FFFFFF;

  background-color: #000000;
  border: none;
  box-shadow: 0px 3px 0px #b2b2b2;

  cursor: pointer;

  &:hover {
    opacity: 0.6;
  }
}

@media (max-width: 1199px) {
  .form {
    padding: 31px 50px 0 51px;
    top: -118px;
    left: 272px;
    width: 413px;
    min-height: 413px;
  }

  .form__caption {
    margin-left: 54px;

    font-family: "Raleway", "Arial", monospace;
    font-weight: 900;
    font-size: 18px;
    letter-spacing: 0.7px;
  }

  .form__label {
    font-size: 14px;
  }

  .form__row {
    margin: 31px 0;
  }

  .form__column {
    width: 119px;

    &--tel {
      width: 150px;
    }
  }

  .form__input {
    font-size: 14px;

    &--mail {
      margin-bottom: 31px;
    }
  }

  .form__text {
    margin-top: 11px;
    margin-left: 30px;

    font-size: 10px;
  }

  .form__link {
    width: 158px;
    margin-top: 0px;
    margin-left: 49px;
    border-bottom: 1px dashed #000000;
  }

  .form__submit {
    margin-left: 73px;
    margin-top: 25px;
    padding: 0;
    width: 164px;
    height: 43px;
    font-size: 16px;
    line-height: 2;
    box-shadow: none;
  }
}

@media (max-width: 767px) {
  .form {
    position: static;
    padding: 0;
    padding-top: 40px;
    width: 100%;
    min-height: 457px;
    transform: none;
  }

  .form__caption {
    margin: 0;
    text-align: center;
  }

  .form__row {
    margin: 23px 0 26px 0;
  }

  .form__label {
    margin-bottom: 6px;
  }

  .form__column--tel {
    width: 119px;
  }

  .form__text {
    margin: 0;
    margin-top: 19px;
    text-align: center;
  }

  .form__link {
    margin: 0 auto;
    width: 160px;
  }

  .form__submit {
    margin: 0 auto;
    margin-top: 28px;
  }
}
